.advantages__section {
  padding-bottom: 32px;

  @include tablet {
    padding-bottom: 41px;
  }

  @include desktop {
    padding-bottom: 80px;
  }
}

.advantages__list {
  @include tablet {
    display: flex;
    gap: 80px;
  }

  @include desktop {
    gap: 32px;
  }
}

.advantages__item {
  @include centerAlingn;
  width: 100%;
  flex-direction: column;
  gap: 16px;

  @media screen and (max-width: 480px) {


    &:not(:last-child) {
      margin-bottom: 28px;
    }
  }

  @include mobile {


    &:not(:last-child) {
      margin-bottom: 32px;
    }
  }

  @include tablet {
    gap: 0px;

    &:not(:last-child) {
      margin-bottom: 0px;
    }

  }
}

.advantages__item::before {
  content: '';
  display: inline-block;
  width: 50px;
  height: 50px;

  fill: black;
  border-radius: 25px;
  background-repeat: no-repeat;

  background-size: 36px 36px;
  background-position: center;
  background-color: $bg-color-button;
  
  animation-name: stretch;
  animation-duration: 2s;
  animation-timing-function: ease-out;
  animation-direction: alternate;
  animation-iteration-count: infinite;
  animation-play-state: running;

  @include tablet {
    margin-bottom: 32px;
  }

  @include desktop {
    margin-bottom: 40px;
  }

  @keyframes stretch {
    0% {
      transform: scale(1);
      background-color: #90EE90;
      border-radius: 100%;
    }

    50% {
      transform: scale(1);
      background-color: #3CB371;
      border-radius: 100%;
    }

    100% {
      transform: scale(1.5);
      background-color: #2E8B57;
    }
  }
}

.advantages__item-cow::before {
  background-image: url(../images/advantages/cow-icon.png);

  @include retina {
    background-image: url(../images/advantages/cow-icon-2x.png);
  }
}

.advantages__item-kg::before {
  background-image: url(../images/advantages/weight-icon.png);

  @include retina {
    background-image: url(../images/advantages/weight-icon-2x.png);
  }
}

.advantages__ice-cream::before {
  background-image: url(../images/advantages/ice-cream-icon.png);

  @include retina {
    background-image: url(../images/advantages/ice-cream-icon-2x.png);
  }
}

.advantages__numbers {
  text-transform: uppercase;
  text-align: center;
  color: $primary-white-color;
  @include font(700, 36, null, null);
  @include tablet {
    font-size: 48px;
    margin-bottom: 12px;
  }

  @include desktop {
    font-size: 58px;
  }
}

.advantages__text {
  width: 230px;
  text-align: center;
  color: $primary-white-color;
  @include font(700, 12, 1.7, null);
  @include tablet {
    @include font(400, 14, 1.7, null);
    width: 152px;
  }

  @include desktop {
    @include font(null, null, 1.8, 0.02);
    width: 368px;
  }
}